#contactBody {
	clear: both;
	background-image: url("https://images.unsplash.com/photo-1527607976958-7cbb4a6d0131?ixlib=rb-1.2.1&ixid=eyJhcHBfaWQiOjEyMDd9&auto=format&fit=crop&w=2743&q=80");
	background-size: cover;
	width: 100%;
	min-height: 100vh;
}

#contactWrapper {
	background-color: black;
	opacity: 0.5;
	width: 100%;
	position: absolute;
}
#contactContent {
	padding-top: 7.5%;
	z-index: 20;
}

.contactLine p {
	color: white;
	font-size: 4.4vw;
	font-family: "Playfair Display", serif;
	margin-bottom: 0;
	transform: scaleY(1.1);
	letter-spacing: 2px;
	opacity: 0.9;
}

.email {
	padding-top: 10%;
}
.email p {
	color: white;
	font-size: 1.4vw;
	font-family: "Playfair Display", serif;
	opacity: 0.9;
}

.w-75 {
	width: 66% !important;
}

.contactLink {
	margin-top: 15%;
}

.linkwrapper {
	padding: 0;
	margin-right: 1%;
}

.contactLink p {
	color: white;
	text-align: center;
	font-family: "Playfair Display", serif;
	opacity: 0.9;
	font-size: 1vw;
}

.contactLink p a {
	color: white;
	font-size: 1vw;
}

.linkwrapper p,
.linkwrapper p:hover,
.linkwrapper p:focus,
.linkwrapper p:active {
	text-decoration: none;
}

.contactLink .col-1 {
	flex: 0 0 7.333333%;
	max-width: 7.333333%;
}

.linkwrapper p::before {
	content: "";
	position: absolute;
	top: 65%;
	height: 1px;
	width: 0%;
	left: 0px;
	background-color: rgba(255, 255, 255, 0.8);
}

.linkwrapper p::after {
	content: "";
	position: absolute;
	top: 65%;
	height: 1px;
	width: 0%;
	left: 3%;
	background-color: rgba(255, 255, 255, 0.8);
	transition: 0.5s ease all 0.3s;
}

.linkwrapper:hover p::before {
	width: 90%;
	transform: translateX(100%);
	background-color: rgba(255, 255, 255, 0);
	transition: 0.5s ease-out;
}

.linkwrapper:hover p::after {
	width: 90%;
	transition: 0.4s ease all;
	transition-delay: 0.3s;
}
